import React from 'react';
import './YoungMenu.css';

import { connect } from 'react-redux';

class YoungMenu extends React.Component {
    constructor ( props ) {
        super ( props );
        this.state = {
            list : [
                "Анатоль Івашчанка",
                "Андрусь Такінданг",
                "Андрэй Хадановіч",
                "Вiка Трэнас",
                "Валярына Кустава",
                "Вера Жыбуль (Джэці)",
                "Вольга Гапеева",
                "Віктар Жыбуль",
                "Віталь Рыжкоў",
                "Глеб Лабадзенка",
                "Дмитрий Строцев",
                "Катерина Зыкова",
                "Кірыла Анохін",
                "Марыя Мартысевіч",
                "Наста Манцэвіч",
                "Павал Свярдлоў",
                "Таццяна Нядбай",
                "Усевалад Сцебурака",
                "Цімур Хоміч"
            ]
        }
    };
    getRenderList () {
        let list = [];
        this.state.list.forEach( el => {
            list.push(
                <li>                    
                    <video
                            className = "YoungMenu_video"                    
                            width = "300px"
                            height = "200px"
                            controls        
                            src={"/video/young/"+ el +".webm" } type='video/webm;'>                            
                            </video>    
                            <p className = "YoungMenu_el_header">{ el }</p>    
                </li>)
        });
        return list;
    }
    render() {
        return (
            <div className = "YoungMenu">
                <ul className = "YoungMenu_older">
                    { this.getRenderList() }
                </ul>               
                <button
                    className = "YoungMenu_but"
                    onClick = { ()=> this.props.BackToMainMenu() }>
                    Вярнуцца да галоўнага меню
                </button>
            </div>
        )
    }
}

const mapState = ( state ) => ({
    AuthorNum : state.numb
})
const mapDispatch = dispatch => {
    return {       
        BackToMainMenu : () => dispatch ({ type : "MAIN_MENU"})        
    }
}

export default connect(mapState, mapDispatch ) (YoungMenu);